.accordion {
  > .card {
    &:not(:first-of-type) {
      .card-header:first-child {
        border-radius: 0;
      }

      &:not(:last-of-type) {
        border-block-end: 0;
        border-radius: 0;
      }
    }

    &:first-of-type {
      border-block-end: 0;
      border-end-end-radius: 0;
      border-end-start-radius: 0;
    }

    &:last-of-type {
      border-start-start-radius: 0;
      border-start-end-radius: 0;
    }
  }

  .card {
    border-width: 0;

    + .card {
      margin-block-start: 1px;
    }
  }

  .card-header {
    padding: 0;
    background-color: transparent;
    font-weight: 400;

    a {
      display: block;
      padding: 12px;
      color: $default-color;
      position: relative;
      background-color: $primary-02;
      border: 1px solid $border;
      border-radius: 5px;
      &[aria-expanded='true'] {
        border-end-end-radius: 0px;
        border-end-start-radius: 0px;
      }
      &:hover,
      &:focus {
        color: $primary;
      }

      &.collapsed {
        color: $gray-700;
        background-color: #fff;
        border-block-end-color: $border;

        &:hover,
        &:focus {
          color: $gray-900;
          background-color: $primary-02;
        }
      }
    }
  }

  .card-body {
    padding: 20px 20px;
    background-color: $white;
    transition: none;
    border: 1px solid #ededf5;
    border-block-start: 0px !important;
    border-radius: 5px;
  }
  .collapse.show {
    .card-body {
      border-radius: 0px 0px 5px 5px;
    }
  }
}

.card .card {
  box-shadow: none !important;
}
.accordion > .card {
  border: 0;
}
/* ###### 3.1 Accordion ###### */

