.toast {
  background-color: rgba(255, 255, 255, 0.95);
  border-color: rgba(28, 39, 60, 0.16);
  box-shadow: 0 2px 3px rgba(28, 39, 60, 0.06);
  border-radius: 3px;
}

.toast-header {
  border-block-end-color: rgba(205, 212, 224, 0.4);

  .close {
    font-weight: 300;
  }
}

.toast {
  max-width: 100%;
  width: 300px;
  overflow: hidden;
  font-size: 0.875rem;
  background-color: rgba(255, 255, 255, 0.85);
  background-clip: padding-box;
  border: 1px solid rgb(229, 232, 243);
  box-shadow: -8px 12px 18px 0 #f4f4fb73;
  backdrop-filter: blur(10px);
  opacity: 0;
  border-radius: 0.25rem;

  &:not(:last-child) {
    margin-block-end: 0.75rem;
  }

  &.showing {
    opacity: 1;
  }

  &.show {
    display: block;
    opacity: 1;
  }

  &.hide {
    display: none;
  }
}

.toast-header {
  display: flex;
  align-items: center;
  padding: 0.25rem 0.75rem;
  color: #2c3e56;
  background-color: rgba(255, 255, 255, 0.85);
  background-clip: padding-box;
  border-block-end: 1px solid $black-05;
}

.toast-body {
  padding: 0.75rem;
  color: #607086;
}

.demo-static-toast .toast {
  opacity: 1;
}

@media (max-width: 568px) {
  .demo-static-toast {
    position: relative !important;
  }
}
