.btn-animation .btn-loaders:after {
  content: '';
  -webkit-animation: loader 500ms infinite linear;
  animation: loader 500ms infinite linear;
  border: 2px solid #fff;
  border-radius: 50%;
  border-inline-end-color: transparent !important;
  border-block-start-color: transparent !important;
  display: block;
  height: 1.4em;
  width: 1.4em;
  position: absolute;
  inset-inline-end: 10px !important;
  inset-block-start: calc(50% - (1.4em / 2));
  -webkit-transform-origin: center;
  transform-origin: center;
  position: absolute !important;
}

.btn-animation .btn-spiner:after {
  content: '';
  -webkit-animation: sk-bounce 1.2s infinite ease-in-out;
  animation: sk-bounce 1.2s infinite ease-in-out;
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
  border-radius: 50%;
  border-inline-end-color: transparent !important;
  border-block-start-color: transparent !important;
  display: block;
  height: 0.5em;
  width: 0.5em;
  position: absolute;
  inset-inline-end: 12px !important;
  inset-block-start: calc(50% - (1.4em / 2));
  -webkit-transform-origin: center;
  transform-origin: center;
  position: absolute !important;
  background: #ffffff;
  width: 14%;
  height: 45%;
}

.btn-animation .btn-loaders {
  color: #fff !important;
  pointer-events: none;
  position: relative;
}

.btn-animation .btn-spiner {
  color: #fff !important;
  pointer-events: none;
  position: relative;
}

