.breadcrumb-header {
  display: flex;
  margin-block-start: 20px;
  margin-block-end: 16px;
  width: 100%;

  .breadcrumb {
    background: transparent !important;
    padding: 0 !important;
    margin-block-end: 0;
    font-size: 13px;
  }
}

.breadcrumb-3 {
  ol {
    position: relative;
    display: block;
    width: 100%;
    text-align: center;
    padding: 10px;
    border-radius: 7px;
  }

  li {
    display: contents;
    text-align: center;
  }
}

.breadcrumb-4 {
  ol {
    position: relative;
    display: block;
    width: 100%;
    text-align: end;
  }

  li {
    display: contents;
    text-align: end;
  }
}

.breadcrumb-item + .breadcrumb-item::before {
  content: '>';
}

@media (max-width: 633px) {
  .breadcrumb-header {
    display: block;
  }
}

@media (max-width: 380px) {
  .breadcrumb-right .btn-outline-primary {
    display: none;
  }
}

.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  padding: 0.75rem 1rem !important;
  margin-block-end: 0;
  list-style: none;
  border-radius: 7px;
}

.breadcrumb-item {
  a {
    color: #7987a1;
  }
  + .breadcrumb-item {
    padding-inline-start: 0.5rem;

    &::before {
      display: inline-block;
      padding-inline-end: 0.5rem;
      color: #e3e6e8b0;
      content: '/';
    }

    &:hover::before {
      text-decoration: underline;
      text-decoration: none;
    }
  }

  &.active {
    color: $primary;
  }
}

.breadcrumb-style1,
.breadcrumb-style2,
.breadcrumb-style3 {
  border-radius: 7px;
}
.breadcrumb-style1 .breadcrumb-item a,
.breadcrumb-style2 .breadcrumb-item a,
.breadcrumb-style3 .breadcrumb-item a {
  color: #6c7b9a;
}

.breadcrumb-style1 .breadcrumb-item a {
  &:hover,
  &:focus {
    color: $primary;
  }
}

.breadcrumb-style2 .breadcrumb-item a {
  &:hover,
  &:focus {
    color: $primary;
  }
}

.breadcrumb-style3 .breadcrumb-item a {
  &:hover,
  &:focus {
    color: $primary;
  }
}

.breadcrumb-style1 .breadcrumb-item.active,
.breadcrumb-style2 .breadcrumb-item.active,
.breadcrumb-style3 .breadcrumb-item.active {
  color: $primary;
}

.breadcrumb-style1 .breadcrumb-item + .breadcrumb-item::before,
.breadcrumb-style2 .breadcrumb-item + .breadcrumb-item::before,
.breadcrumb-style3 .breadcrumb-item + .breadcrumb-item::before {
  color: #cfd4e1;
}

.breadcrumb-style2 .breadcrumb-item + .breadcrumb-item::before,
.breadcrumb-style3 .breadcrumb-item + .breadcrumb-item::before {
  width: 16px;
  text-align: center;
  font-family: 'Ionicons';
}

.breadcrumb-style2 .breadcrumb-item + .breadcrumb-item::before {
  content: '\f3d1';
}

.breadcrumb-style3 .breadcrumb-item + .breadcrumb-item::before {
  content: '\f287';
}

.main-content-breadcrumb {
  display: flex;
  align-items: center;
  font-size: 10px;
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: $gray-500;
  margin-block-end: 10px;

  span {
    position: relative;

    + span::before {
      content: '\f3d1';
      font-family: 'Ionicons';
      font-size: 12px;
      font-weight: 300;
      display: inline;
      margin: 0 5px;
      color: $gray-400;
      line-height: 0;
      position: relative;
      inset-block-start: 1px;
    }

    &:last-child {
      color: $gray-700;
    }
  }
}

.breadcrumb-item + .breadcrumb-item::before {
  float: none;
  padding-inline-end: 0.5rem;
  color: #d6dfe7;
  content: var(--bs-breadcrumb-divider, '\e933');
  font-family: 'feather' !important;
}
