/* ###### 3.6 Forms ###### */

.form-control {
  height: 38px;
  border-radius: 0;

  &:focus {
    border-color: $gray-500;
    box-shadow: none;
  }
}
[type='search'] {
  &::-webkit-search-cancel-button,
  &::-webkit-search-decoration {
    -webkit-appearance: none;
  }
}
.form-label {
  display: block;
  margin-block-end: 5px;
  color: #9393b5;
}

.form-control {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0.375rem 0.75rem;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 2;
  color: $gray-700;
  background-color: $white;
  background-clip: padding-box;
  border: 1px solid $border;
  border-radius: 5px;
  transition:
    border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out;

  &::-ms-expand {
    background-color: transparent;
    border: 0;
  }

  &:focus {
    color: $gray-700;
    background-color: $white;
    border-color: $primary;
    outline: 0;
    box-shadow: none;
  }

  &::placeholder {
    color: $gray-600;
    opacity: 1;
  }

  &:disabled,
  &[readonly] {
    background-color: $background;
    opacity: 1;
  }
}

@media (prefers-reduced-motion: reduce) {
  .form-control {
    transition: none;
  }
}

select.form-control:focus::-ms-value {
  color: $gray-700;
  background-color: $white;
}

.form-control-file,
.form-control-range {
  display: block;
  width: 100%;
}

.col-form-label {
  padding-block-start: calc(0.375rem + 1px);
  padding-block-end: calc(0.375rem + 1px);
  margin-block-end: 0;
  font-size: inherit;
  line-height: 1.5;
}

.col-form-label-lg {
  padding-block-start: calc(0.5rem + 1px);
  padding-block-end: calc(0.5rem + 1px);
  font-size: 1.09375rem;
  line-height: 1.5;
}

.col-form-label-sm {
  padding-block-start: calc(0.25rem + 1px);
  padding-block-end: calc(0.25rem + 1px);
  font-size: 0.76562rem;
  line-height: 1.5;
}

.form-control-plaintext {
  display: block;
  width: 100%;
  padding-block-start: 0.375rem;
  padding-block-end: 0.375rem;
  margin-block-end: 0;
  line-height: 1.5;
  color: $default-color;
  background-color: transparent;
  border: solid transparent;
  border-width: 1px 0;

  &.form-control-sm,
  &.form-control-lg {
    padding-inline-end: 0;
    padding-inline-start: 0;
  }
}

.form-control-sm {
  height: calc(1.5em + 0.5rem + 2px);
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0.2rem;
}

.form-control-lg {
  height: calc(1.5em + 1rem + 2px);
  padding: 0.5rem 1rem;
  font-size: 1.09375rem;
  line-height: 1.5;
  border-radius: 0.3rem;
}

select.form-control {
  &[size],
  &[multiple] {
    height: auto;
  }
}

textarea.form-control {
  height: auto;
}

.form-group {
  margin-block-end: 1rem;
}

.form-text {
  display: block;
  margin-block-start: 0.25rem;
}

.form-row {
  display: flex;
  flex-wrap: wrap;
  margin-inline-end: -5px;
  margin-inline-start: -5px;

  > {
    .col,
    [class*='col-'] {
      padding-inline-end: 5px;
      padding-inline-start: 5px;
    }
  }
}

.form-check {
  position: relative;
  display: block;
  padding-inline-start: 1.25rem;
}

.form-check-input {
  // position: absolute;
  margin-block-start: 0.3rem;
  margin-inline-start: -1.25rem;

  &:disabled ~ .form-check-label {
    color: $gray-600;
  }
}

.form-check-label {
  margin-block-end: 0;
}

.form-check-inline {
  display: inline-flex;
  align-items: center;
  padding-inline-start: 0;
  margin-inline-end: 0.75rem;

  .form-check-input {
    position: static;
    margin-block-start: 0;
    margin-inline-end: 0.3125rem;
    margin-inline-start: 0;
  }
}

.valid-feedback {
  display: none;
  width: 100%;
  margin-block-start: 0.25rem;
  font-size: 80%;
  color: $success;
}

.valid-tooltip {
  position: absolute;
  inset-block-start: 100%;
  z-index: 5;
  display: none;
  max-width: 100%;
  padding: 0.25rem 0.5rem;
  margin-block-start: 0.1rem;
  font-size: 0.76562rem;
  line-height: 1.5;
  color: $white;
  background-color: rgba(59, 176, 1, 0.9);
  border-radius: 3px;
}

