.dropup,
.dropright,
.dropdown,
.dropleft {
  position: relative;
}

.dropdown-toggle {
  white-space: nowrap;

  &::after {
    display: inline-block;
    margin-inline-start: 0.255em;
    vertical-align: 0.255em;
    content: '';
    border-block-start: 0.3em solid;
    border-inline-end: 0.3em solid transparent;
    border-block-end: 0;
    border-inline-start: 0.3em solid transparent;
  }

  &:empty::after {
    margin-inline-start: 0;
  }
}

.dropdown-menu {
  position: absolute;
  inset-block-start: 100%;
  inset-inline-start: 0;
  z-index: 999;
  display: none;
  float: left;
  min-width: 10rem !important;
  padding: 0.5rem 0;
  margin-block-start: 0.125rem;
  margin-inline-end: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  font-size: 0.875rem;
  color: $default-color;
  text-align: start;
  list-style: none;
  background-color: $white;
  background-clip: padding-box;
  border: 1px solid $border;
  border-radius: 5px !important;
  box-shadow: 0px 16px 18px rgba(135, 135, 182, 0.1) !important;
}

.dropdown-menu-start {
  inset-inline-end: auto;
  inset-inline-start: 0;
}

.dropdown-menu-end {
  inset-inline-end: 0;
  inset-inline-start: auto;
}

@media (min-width: 576px) {
  .dropdown-menu-sm-left {
    inset-inline-end: auto;
    inset-inline-start: 0;
  }

  .dropdown-menu-sm-right {
    inset-inline-end: 0;
    inset-inline-start: auto;
  }
}

@media (min-width: 768px) {
  .dropdown-menu-md-left {
    inset-inline-end: auto;
    inset-inline-start: 0;
  }

  .dropdown-menu-md-right {
    inset-inline-end: 0;
    inset-inline-start: auto;
  }
}

@media (min-width: 992px) {
  .dropdown-menu-lg-left {
    inset-inline-end: auto;
    inset-inline-start: 0;
  }

  .dropdown-menu-lg-right {
    inset-inline-end: 0;
    inset-inline-start: auto;
  }
}

@media (min-width: 1200px) {
  .dropdown-menu-xl-left {
    inset-inline-end: auto;
    inset-inline-start: 0;
  }

  .dropdown-menu-xl-right {
    inset-inline-end: 0;
    inset-inline-start: auto;
  }
}

.dropup {
  .dropdown-menu {
    inset-block-start: auto;
    inset-block-end: 100%;
    margin-block-start: 0;
    margin-block-end: 0.125rem;
  }

  .dropdown-toggle {
    &::after {
      display: inline-block;
      margin-inline-start: 0.255em;
      vertical-align: 0.255em;
      content: '';
      border-block-start: 0;
      border-inline-end: 0.3em solid transparent;
      border-block-end: 0.3em solid;
      border-inline-start: 0.3em solid transparent;
    }

    &:empty::after {
      margin-inline-start: 0;
    }
  }
}

.dropright {
  .dropdown-menu {
    inset-block-start: 0;
    inset-inline-end: auto;
    inset-inline-start: 100%;
    margin-block-start: 0;
    margin-inline-start: 0.125rem;
  }

  .dropdown-toggle {
    &::after {
      display: inline-block;
      margin-inline-start: 0.255em;
      vertical-align: 0.255em;
      content: '';
      border-block-start: 0.3em solid transparent;
      border-inline-end: 0;
      border-block-end: 0.3em solid transparent;
      border-inline-start: 0.3em solid;
    }

    &:empty::after {
      margin-inline-start: 0;
    }

    &::after {
      vertical-align: 0;
    }
  }
}

.dropleft {
  .dropdown-menu {
    inset-block-start: 0;
    inset-inline-end: 100%;
    inset-inline-start: auto;
    margin-block-start: 0;
    margin-inline-end: 0.125rem;
  }

  .dropdown-toggle {
    &::after {
      display: inline-block;
      margin-inline-start: 0.255em;
      vertical-align: 0.255em;
      content: '';
      display: none;
    }

    &::before {
      display: inline-block;
      margin-inline-end: 0.255em;
      vertical-align: 0.255em;
      content: '';
      border-block-start: 0.3em solid transparent;
      border-inline-end: 0.3em solid;
      border-block-end: 0.3em solid transparent;
    }

    &:empty::after {
      margin-inline-start: 0;
    }

    &::before {
      vertical-align: 0;
    }
  }
}

.dropdown-menu {
  &[data-popper-placement^='top'],
  &[data-popper-placement^='right'],
  &[data-popper-placement^='bottom'],
  &[data-popper-placement^='left'] {
    inset-inline-end: auto;
    inset-block-end: auto;
  }
}

.dropdown-divider {
  height: 0;
  margin: 0.5rem 0;
  overflow: hidden;
  border-block-start: 1px solid $gray-200;
}

.dropdown-item {
  display: block;
  width: 100%;
  padding: 0.25rem 1.5rem;
  clear: both;
  font-weight: 400;
  color: $gray-900;
  text-align: inherit;
  white-space: nowrap;
  background-color: transparent;
  border: 0;

  &:hover,
  &:focus {
    color: #141c2b;
    text-decoration: none;
    background-color: #fcfcff;
  }

  &.active,
  &:active {
    color: #141c2b;
    text-decoration: none;
    background-color: #fcfcff;
  }

  &.disabled,
  &:disabled {
    color: $gray-600;
    pointer-events: none;
    background-color: transparent;
  }
}

.dropdown-menu.show {
  display: block;
}

.dropdown-header {
  display: block;
  padding: 0.5rem 1.5rem;
  margin-block-end: 0;
  font-size: 0.76562rem;
  color: $gray-600;
  white-space: nowrap;
}

.dropdown-item-text {
  display: block;
  padding: 0.25rem 1.5rem;
  color: $gray-900;
}

.dropdown-toggle-split {
  padding-inline-end: 0.5625rem;
  padding-inline-start: 0.5625rem;

  &::after {
    margin-inline-start: 0;
  }
}

.dropup .dropdown-toggle-split::after,
.dropright .dropdown-toggle-split::after {
  margin-inline-start: 0;
}

.dropleft .dropdown-toggle-split::before {
  margin-inline-end: 0;
}

/* ###### 3.5 Dropdown ###### */

.dropdown-menu {
  padding: 0;
  border-width: 1px;
  border-radius: 5px;
}

.dropdown-item {
  padding: 8px 15px;
  font-size: 13px;
}

.dropdown-header {
  padding-inline-start: 15px;
  padding-inline-end: 15px;
}

@media (min-width: 768px) {
  .dropdown {
    &.d-cart .dropdown-menu,
    &.message .dropdown-menu {
      width: 20.5rem !important;
    }
  }
}

.user-lock .dropdown {
  position: absolute;
  inset-inline-end: 20px;
}

.main-dropdown-form-demo {
  .static-dropdown {
    padding: 20px;
    background-color: $gray-200;
    display: inline-flex;
    justify-content: center;

    .dropdown-menu {
      display: block;
      position: static;
      float: none;
    }
  }

  .dropdown-menu {
    padding: 20px;
  }

  .dropdown-title {
    font-size: 20px;
    font-weight: 700;
    color: $gray-900;
    margin-block-end: 5px;
  }
}

@media (min-width: 576px) {
  .main-dropdown-form-demo .static-dropdown {
    padding: 40px;
  }
}

@media (min-width: 576px) {
  .main-dropdown-form-demo .dropdown-menu {
    width: 300px;
    padding: 30px 25px;
  }
}
