.list-group-flush {
  .list-group-item {
    border-inline-end: 0;
    border-inline-start: 0;
    border: 0 !important;
    border-radius: 0;

    &:last-child {
      margin-block-end: -1px;
    }
  }

  &:first-child .list-group-item:first-child {
    border-block-start: 0;
  }

  &:last-child .list-group-item:last-child {
    margin-block-end: 0;
    border-block-end: 0;
  }
}

