/* ###### 3.14 Table ###### */

.table {
  color: $gray-800;

  thead {
    th,
    td {
      font-weight: 600;
      font-size: 14px;
      letter-spacing: 0.5px;
      text-transform: capitalize;
      border-block-end-width: 1px;
      border-block-start-width: 0;
      padding-block-start: 10px;
      padding-inline-end: 15px;
      padding-block-end: 10px;
      padding-inline-start: 15px;
    }
  }

  tbody tr {
    background-color: $white-5;

    th {
      font-weight: 500;
    }
  }

  th,
  td {
    padding: 9px 15px;
    line-height: 1.462;
    font-size: 13px;
  }
}

.table-striped tbody tr:nth-of-type(4n + 2) {
  background-color: rgba(238, 238, 247, 0.5);
}

.table-bordered thead {
  th,
  td {
    border-block-start-width: 1px;
    padding-block-start: 11px;
    padding-block-end: 11px;
    // background-color: $white;
  }
}

.table {
  width: 100%;
  margin-block-end: 1rem;
  color: $default-color;

  th,
  td {
    padding: 0.75rem;
    vertical-align: middle;
    border-block-start: 1px solid $gray-200;
  }

  thead th {
    vertical-align: bottom;
  }

  tbody + tbody {
    border-block-start: 2px solid transparent !important;
  }
}

.table-sm {
  th,
  td {
    padding: 0.3rem;
  }
}

.table-bordered {
  border: 1px solid $gray-200;

  th,
  td {
    border: 1px solid $gray-200;
  }
}

.table-borderless {
  th,
  td,
  thead th,
  tbody + tbody {
    border: 0;
  }
}

.table-striped tbody tr:nth-of-type(even) {
  background-color: $gray-100;
}

.table-hover tbody tr:hover {
  color: $default-color;
  background-color: $bg-hover;
}

.table-active {
  background-color: rgba(0, 0, 0, 0.075);

  > {
    th,
    td {
      background-color: rgba(0, 0, 0, 0.075);
    }
  }
}

.table-hover .table-active:hover {
  background-color: rgba(0, 0, 0, 0.075);

  > {
    td,
    th {
      background-color: rgba(0, 0, 0, 0.075);
    }
  }
}

.table {
  .thead-dark th {
    color: $white;
    background-color: $gray-800;
    border-color: #49597b;
  }

  .thead-light th {
    color: $gray-700;
    background-color: $gray-200;
    border-color: $gray-300;
  }
}

.table-dark {
  color: $white;
  background-color: $gray-800;

  th,
  td,
  thead th {
    border-color: #49597b;
  }

  &.table-bordered {
    border: 0;
  }

  &.table-striped tbody tr:nth-of-type(odd) {
    background-color: $white-05;
  }

  &.table-hover tbody tr:hover {
    color: $white;
    background-color: $white-75;
  }
}

@media (max-width: 575.98px) {
  .table-responsive-sm {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;

    > .table-bordered {
      border: 0;
    }
  }
}

@media (max-width: 767.98px) {
  .table-responsive-md {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;

    > .table-bordered {
      border: 0;
    }
  }
}

@media (max-width: 991.98px) {
  .table-responsive-lg {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;

    > .table-bordered {
      border: 0;
    }
  }
}

@media (max-width: 1199.98px) {
  .table-responsive-xl {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;

    > .table-bordered {
      border: 0;
    }
  }
}

.table-responsive {
  display: block;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;

  > .table-bordered {
    border: 0;
  }
}

@media print {
  .table {
    border-collapse: collapse !important;

    td,
    th {
      background-color: $white !important;
    }
  }

  .table-bordered {
    th,
    td {
      border: 1px solid $border !important;
    }
  }

  .table-dark {
    color: inherit;

    th,
    td,
    thead th,
    tbody + tbody {
      border-color: $gray-300;
    }
  }

  .table .thead-dark th {
    color: inherit;
    border-color: $gray-300;
  }
}

.table.table-clean {
  td {
    .value {
      font-size: 0.9rem;
      line-height: 1.6;
      font-weight: 500;
    }

    .sub-value {
      font-size: 0.72rem;
      color: $gray-600;
    }
  }

  tr:first-child td {
    border-block-start: none;
  }

  td {
    padding-inline-start: 0px;
    padding-inline-end: 0px;
    border-block-start-color: $black-05;
  }

  th,
  td {
    padding: 7px 20px !important;
  }
}

@media (max-width: 1199px) {
  .table-responsive {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
}

.table > :not(:last-child) > :last-child > * {
  border-block-end-color: $border;
}

.table-bordered > :not(caption) > * {
  border-width: 0;
}

.table-striped > tbody > tr:nth-of-type(odd) {
  --bs-table-accent-bg: rgb(255, 255, 255);
  color: $dark;
}

tbody,
td,
tfoot,
th,
thead,
tr {
  border-color: $border;
  border-style: none;
  border-width: 0;
}

.table-hover > tbody > tr:hover {
  --bs-table-accent-bg: none !important;
  background: $bg-hover;
}

@media (min-width: 768px) {
  .table-responsive.deleted-table .data-table-btn {
    position: absolute;
    inset-inline-start: 185px;
    z-index: 999;
  }
}

@media (min-width: 1319px) {
  .table-responsive.export-table .dt-buttons.btn-group {
    position: absolute;
    inset-block-start: 0;
    inset-inline-start: 172px;
  }
}

.table > :not(:first-child) {
  border-block-start: 1px solid $border !important;
}

.table-striped > tbody > tr:nth-of-type(odd) > * {
  --bs-table-accent-bg: $white !important;
}

@media (max-width: 583px) {
  .dt-buttons {
    .btn,
    .sp-container button {
      padding: 0.5rem 0.1rem;
    }
  }
}

.table > :not(caption) > * > * {
  background-color: transparent;
}

