.dark-theme .intl-tel-input {
  input {
    border: 1px solid rgba(225, 230, 241, 0.1);
    background: transparent;
    color: #fff;
  }

  .flag-dropdown {
    .selected-flag {
      background: #383d52;

      .down-arrow {
        border-block-start: 4px solid #fff;
      }
    }

    .country-list {
      .country.highlight {
        background-color: rgb(36, 47, 74);
        border: 0 !important;
      }

      box-shadow: 1px 1px 4px rgb(0, 0, 0);
      background-color: $dark-theme;

      .divider {
        border-block-end: 1px solid rgba(234, 240, 255, 0.2);
      }
    }
  }
}
