/*---- Tags-----*/

.tag {
  font-size: 0.75rem;
  color: #8189b6;
  border: 1px solid $border;
  border-radius: 3px;
  padding: 2px 0.55rem;
  line-height: 2em;
  display: -ms-inline-flexbox;
  display: inline-flex;
  cursor: default;
  font-weight: 400;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

a.tag {
  text-decoration: none;
  cursor: pointer;
  transition:
    0.3s color,
    0.3s background;

  &:hover {
    background-color: rgba(110, 118, 135, 0.2);
    color: inherit;
  }
}

.tag-addon {
  display: inline-block;
  padding: 0 0.5rem;
  color: inherit;
  text-decoration: none;
  background: #f9f9fb;
  margin-block-start: 0px;
  margin-inline-end: -0.4rem;
  margin-block-end: 0px;
  margin-inline-start: 0.5rem;
  text-align: center;
  min-width: 1.5rem;

  &:last-child {
    border-radius: 3px;
  }

  i {
    vertical-align: middle;
    margin: 0 -0.25rem;
    line-height: 1.5px;
    font-size: 13px;
  }
}

a.tag-addon {
  text-decoration: none;
  cursor: pointer;
  transition:
    0.3s color,
    0.3s background;

  &:hover {
    background: rgb(238, 238, 255);
    color: inherit;
  }
}

.tag-avatar {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 3px 0 0 3px;
  margin-block-start: 0px;
  margin-inline-end: 0.5rem;
  margin-block-end: 0px;
  margin-inline-start: -0.5rem;
}

