.tree {
  margin: 0;
  padding: 0;
  list-style: none;

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    margin-inline-start: 1em;
    position: relative;

    ul {
      margin-inline-start: 0.5em;
    }

    &:before {
      content: '';
      display: block;
      width: 0;
      position: absolute;
      inset-block-start: 0;
      inset-block-end: 0;
      inset-inline-start: -10px;
      border-inline-start: 1px solid #ededf5;
    }
  }

  li {
    margin: 0;
    padding: 0.5em 1em 0.2em 1em;
    line-height: 2em;
    color: #1a1630;
    position: relative;
    border: 1px solid #ebecf1;
    margin: 10px 0;
    border-radius: 5px;
    line-height: 30px;

    i {
      margin-inline-end: 10px;
      font-weight: bold;
      font-size: 16px;

      /* padding-block-start: 10px; */
      line-height: 1;
    }
  }

  ul li {
    &:before {
      content: '';
      display: block;
      width: 10px;
      height: 0;
      border-block-start: 2px solid #e5e5ea;
      margin-block-start: -1px;
      position: absolute;
      inset-block-start: 22px;
      inset-inline-start: -10px;
    }

    &:last-child:before {
      background: transparent;
      // height: auto;
      inset-block-start: 22px;
      inset-block-end: 0;
    }
  }
}

.indicator {
  margin-inline-end: 5px;
  color: #4b10a4;
}

.tree li {
  a {
    text-decoration: none;
    color: #1a1630;
    font-size: 15px;
  }

  button {
    text-decoration: none;
    color: #29273c;
    border: none;
    background: transparent;
    margin: 0px 0px 0px 0px;
    padding: 0px 0px 0px 0px;
    outline: 0;

    &:active,
    &:focus {
      text-decoration: none;
      color: #29273c;
      border: none;
      background: transparent;
      margin: 0px 0px 0px 0px;
      padding: 0px 0px 0px 0px;
      outline: 0;
    }
  }

  &.branch {
    background: #ffffff;

    li {
      background: #ffffff;
    }
  }
}
